	module.exports = {"data":{"markdownRemark":{"html":"<h1>Who am I? ü§ñ</h1>\n<p>I am a programmer that codes in Windows, Linux and Mac platform. My preferred language is JavaScript, so much love that I did some iOT application using JS on Arduino.</p>\n<h1>Why gatsby as static site generator?</h1>\n<p>Gatsby is fun and combining things that I love to use~</p>\n<p>With emoji (üëæ) + React.JS + GraphQL + Markdown + Vim</p>\n<p>These tools are all in one! and No Database needed, free hosting (with github), secure (so-called since no database) and I can use JS in this üòç</p>\n<h1>What will I post?</h1>\n<p>I will post guides, notes, stuff I did as a project, my on going project and many more!</p>\n<h1>What I normally do?</h1>\n<ul>\n<li>Back end Web development with Ruby on Rails and Meteor + Node.js</li>\n<li>App development (on iOS with Swift üçè)</li>\n<li>Embedded System (Arduino and PIC)</li>\n<li>Machine Learning? (I just got started with this)</li>\n</ul>\n<h1>Disclaimer</h1>\n<ul>\n<li>I code a lot on daily basis.</li>\n<li>I will use a lot of emojis üåöüåù</li>\n</ul>\n<h1>Some of the good reference for building Gatsby</h1>\n<ul>\n<li><a href=\"https://www.gatsbyjs.org/blog/2017-07-19-creating-a-blog-with-gatsby/\">Blog post by Dustin Schau</a></li>\n<li><a href=\"https://www.gatsbyjs.org\">gatsbyjs official website</a></li>\n</ul>","frontmatter":{"date":"August 20, 2017","path":"/first-post","title":"About this blog"}}},"pathContext":{}}

